import React, { Fragment, Component } from 'react'
import Link from 'next/link'
import { Row, Col, Icon, Drawer } from 'antd'
import styleTagWithCss from "./NavbarStyles";

class Navbar extends Component {
    render() {
        return (
            <Fragment>
                {styleTagWithCss()}
                {/* <div className='navbar'>
                    Navbar2.js
                </div> */}
                <Row type="flex" align="middle" gutter={8} className='navbar'>
                    <Col>Home </Col>
                    <Col>Contact</Col>
                    <Icon class="ml-auto" type="user" theme="outlined" style={{ fontSize: '32px', color: '#08c', lineHeight: 1 }} />
                </Row>

                <Drawer
                    title="Basic Drawer"
                    placement={this.state.placement}
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visible}
                >
                    <p>Some contents...</p>
                    <p>Some contents...</p>
                    <p>Some contents...</p>
                </Drawer>
            </Fragment>
        )
    }
}

export default Navbar